import { Injectable } from '@angular/core';
import { deepMerge } from '@vx/util';
import { NzDrawerOptions, NzDrawerService } from 'ng-zorro-antd/drawer';
import { Observable, Observer } from 'rxjs';

export interface DrawerHelperOptions {
  /**
   * å¤§å°ï¼è¥å¼ä¸ºæ°å¼ç±»åï¼åæ ¹æ® `nzPlacement` èªå¨è½¬åä¸º `nzHeight` æ `nzWidth`ï¼ä¾å¦ï¼lgã600ï¼é»è®¤ï¼`md`
   *
   * | ç±»å | é»è®¤å¤§å° |
   * | --- | ------ |
   * | `sm` | `300` |
   * | `md` | `600` |
   * | `lg` | `900` |
   * | `xl` | `1200` |
   *
   * > ä»¥ä¸å¼ï¼å¯éè¿è¦çç¸åºçLESSåæ°èªè¡è°æ´
   */
  size?: 'sm' | 'md' | 'lg' | 'xl' | number;
  /**
   * æ¯å¦åå«åºé¨å·¥å·æ¡ï¼é»è®¤ï¼`true`
   */
  footer?: boolean;
  /**
   * åºé¨å·¥å·æ¡é«åº¦ï¼é»è®¤ï¼`55`
   */
  footerHeight?: number;
  /** æ¯å¦ç²¾åï¼é»è®¤ï¼`true`ï¼ï¼è¥è¿åå¼éç©ºå¼ï¼`null`æ`undefined`ï¼è§ä¸ºæåï¼å¦åè§ä¸ºéè¯¯ */
  exact?: boolean;
  /** æ½å± [NzDrawerOptions](https://ng.ant.design/components/drawer/zh#nzdraweroptions) åæ° */
  drawerOptions?: NzDrawerOptions;
}

/**
 * æ½å±è¾å©ç±»
 *
 * **æ³¨æï¼** æå»ºç»æé½å¯è¢«è®¢éï¼ä½æ°¸è¿é½ä¸ä¼è§¦å `observer.error`
 *
 * @example
 * this.drawerHelper.create('Edit', FormEditComponent, { i }).subscribe(res => this.load());
 * // å¯¹äºç»ä»¶çæå&å³é­çå¤çè¯´æ
 * // æå
 * this.NzDrawerRef.close(data);
 * this.NzDrawerRef.close(true);
 * // å³é­
 * this.NzDrawerRef.close();
 * this.NzDrawerRef.close(false);
 */
@Injectable({ providedIn: 'root' })
export class DrawerHelper {
  constructor(private srv: NzDrawerService) {}

  /**
   * æå»ºä¸ä¸ªæ½å±
   */
  create(title: string, comp: any, params?: any, options?: DrawerHelperOptions): Observable<any> {
    options = deepMerge(
      {
        size: 'md',
        footer: true,
        footerHeight: 55,
        exact: true,
        drawerOptions: {
          nzPlacement: 'right',
          nzWrapClassName: '',
        },
      },
      options,
    );
    return new Observable((observer: Observer<any>) => {
      const { size, footer, footerHeight, drawerOptions } = options as DrawerHelperOptions;
      const defaultOptions: NzDrawerOptions = {
        nzContent: comp,
        nzContentParams: params,
        nzTitle: title,
      };

      if (typeof size === 'number') {
        defaultOptions[
          drawerOptions!.nzPlacement === 'top' || drawerOptions!.nzPlacement === 'bottom' ? 'nzHeight' : 'nzWidth'
        ] = options!.size;
      } else {
        defaultOptions.nzWrapClassName = (drawerOptions!.nzWrapClassName + ` drawer-${options!.size}`).trim();
        delete drawerOptions!.nzWrapClassName;
      }

      if (footer) {
        const { nzPlacement, nzHeight } = drawerOptions as NzDrawerOptions;
        // Should be header * footer, because of includes header
        const reduceHeight = footerHeight! * 2 - 2;
        if (nzPlacement === 'left' || nzPlacement === 'right') {
          defaultOptions.nzBodyStyle = {
            height: `calc(100% - ${reduceHeight}px)`,
            overflow: 'auto',
          };
        } else {
          defaultOptions.nzBodyStyle = {
            height: `${+(nzHeight || 256) - reduceHeight}px`,
            overflow: 'auto',
          };
        }
      }

      const subject = this.srv.create({ ...defaultOptions, ...drawerOptions });
      const afterClose$ = subject.afterClose.subscribe((res: any) => {
        if (options!.exact === true) {
          if (res != null) {
            observer.next(res);
          }
        } else {
          observer.next(res);
        }
        observer.complete();
        afterClose$.unsubscribe();
      });
    });
  }

  /**
   * æå»ºä¸ä¸ªæ½å±ï¼ç¹å»èå±ä¸åè®¸å³é­
   */
  static(title: string, comp: any, params?: any, options?: DrawerHelperOptions): Observable<any> {
    const drawerOptions = {
      nzMaskClosable: false,
      ...(options && options.drawerOptions),
    };
    return this.create(title, comp, params, { ...options, drawerOptions });
  }
}
