export const VX_I18N_TOKEN = new InjectionToken<VXI18NService>('vxTranslatorToken', {
  providedIn: 'root',
  factory: VX_I18N_TOKEN_FACTORY,
});

export function VX_I18N_TOKEN_FACTORY() {
  return new VXI18NServiceFake();
}

@Injectable({ providedIn: 'root' })
export class VXI18NServiceFake implements VXI18NService {
  private change$ = new BehaviorSubject<string | null>(null);

  get change(): Observable<string> {
    return this.change$.asObservable().pipe(filter(w => w != null)) as Observable<string>;
  }

  use(lang: string): void {
    this.change$.next(lang);
  }

  getLangs(): any[] {
    return [];
  }

  fanyi(key: string) {
    return key;
  }
}
